import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, ReactiveFormsModule } from '@angular/forms';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent implements OnInit {

  employeeForm: FormGroup;
  employeeList = [];
  indexValue=0;

  onSubmit(): void {
    //console.log(this.employeeForm.value);
    //alert(JSON.stringify(this.employeeForm.value));
    // alert("Hi  your email id is "+this.employeeForm.controls.email.value);
    // alert("Hi  your email id is "+this.employeeForm.get('email').value);

    this.employeeList.push(this.employeeForm.value)
  }

  onUpdate(data,i){  
    this.indexValue=i;
    //alert(JSON.stringify(data)+i);
  this.employeeForm.patchValue({
    fullName:data.fullName,
    email:data.email
  });
  }
edit(data){
  alert(this.indexValue);
  this.employeeList[this.indexValue].fullName=data.fullName;
  this.employeeList[this.indexValue].email=data.email;
}

  constructor() { }

  ngOnInit() {
    this.employeeForm = new FormGroup({
      fullName: new FormControl(),
      email: new FormControl()
    });

  }

}
